<template lang="html">
  <section class="exchangelist">
    <v-tabs fixed-tabs>
      <v-tab>
        Exchange List
      </v-tab>
      <v-tab>
        No Match List
      </v-tab>
      <v-tab-item>
        <v-card>
          <v-toolbar color="gray" light>
            <!-- <v-toolbar-title>Exchange List</v-toolbar-title> -->
            <v-form ref="form">
              <v-text-field
                full-width
                v-model="currentName"
                :counter="255"
                label="Add Member"
                required
              ></v-text-field>
            </v-form>
            <v-btn
              icon
              color="success"
              @click="addMember()"
              :disabled="this.currentName == ''"
            >
              <v-icon>add</v-icon>
            </v-btn>
          </v-toolbar>

          <v-alert :value="this.addMemberError != ''" type="error" dismissible>
            {{ addMemberError }}
          </v-alert>

          <v-list two-line>
            <template v-for="item in members">
              <v-list-tile :key="item.name" avatar>
                <v-list-tile-avatar>
                  <v-icon>person</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title v-html="item.name"></v-list-tile-title>
                  <!-- <v-list-tile-sub-title
                v-html="item.subtitle"
              ></v-list-tile-sub-title> -->
                </v-list-tile-content>

                <v-list-tile-action>
                  <v-btn icon ripple>
                    <v-icon color="blue lighten-1">edit</v-icon>
                  </v-btn>
                </v-list-tile-action>
                <v-list-tile-action @click="removeMember(item.name)">
                  <v-btn icon ripple>
                    <v-icon color="red lighten-1">remove_circle</v-icon>
                  </v-btn>
                </v-list-tile-action>
              </v-list-tile>
            </template>
          </v-list>
        </v-card>
      </v-tab-item>
    </v-tabs>
  </section>
</template>

<script lang="js">

import MemberService from '../services/memberService'

  export default  {
    name: 'exchangelist',
    props: [],
    async mounted() {

     try {
          const members = await MemberService.getMembers();
          members.forEach(member => this.members.push(member));
       } catch (error) {

         // eslint-disable-next-line no-console
         console.log(error);

       }

    },

    computed: {

        addEnabled:function(){

           return this.currentName == '';
        },
        rules () {
        const rules = []

        if (this.currentName) {
          const rule =
            v => (v || '').length <= this.max ||
              `A maximum of ${this.max} characters is allowed`

          rules.push(rule)
        }

        return rules
      }
    },

    data() {
      return {

         addMemberError:'',
         currentName: '',
         members: []
      }
    },
    methods: {

 validateField () {
        this.$refs.form.validate()
      },
        addMember: async function(){

           if(this.currentName){

             var nameToAdd = this.currentName.trim();

              if(this.memberExist(nameToAdd))
              {
                  this.addMemberError = 'Member already exists.'
                  return;
              }

              //  this.members.push({
              //    name: nameToAdd
              //  });s

              try {

                  var addedMember = await MemberService.addMember({name: this.currentName });

              if(addedMember)
              {
                   this.currentName = '';
               this.addMemberError ='';
                this.members.push(addedMember);
              }

              } catch (error) {

                  this.addMemberError = 'error occurred adding new member';
              }


           }
        },

        editMember: function(){

        },

        removeMember: function(member){

               var memberNdx = this.members.findIndex((e)=>{
                    return  e.name === member;
               });

             if(memberNdx > -1)
             {
                this.members.splice(memberNdx, 1);
             }
     },

        memberExist: function (member){

            return this.members.find(function(element) {
            return  element.name === member;
          });
        }
    }
}
</script>

